<template>
  <div>
    <label>Введите жанр:</label>
    <input type="text" v-model="genre">
    <label>Введите минимальный рейтинг (опционально):</label>
    <input type="text" v-model="rating">
    <button @click="search">Поиск</button>

    <div v-if="loading">Загрузка...</div>
    <div v-else-if="film">
      <h2>Найденный фильм:</h2>
      <p>{{ film.name_russian }} {{ film.age_restriction }}+</p>
      <h2>Описание:</h2>
      <p>{{ film.description }}</p>
      <h2>Оценка:</h2>
      <p>{{ film.rating_kp }}</p>
      <h2>Жанры:</h2>
      <ul>
        <li v-for="genre in film.genres" :key="genre.id">{{ genre.name_ru }}</li>
      </ul>
      <h2>Страны:</h2>
      <p>{{ film.country_ru }}</p>
      <h2>Продолжительность:</h2>
      <p>{{ film.time }}</p>
      <h2>Год:</h2>
      <p>{{ film.year }}</p>
      <h2>
      <a :href=link_kp> На кинопоиске</a>
      </h2>
      <img :src=film.small_poster>
      <iframe width="560" height="315" :src=film.trailer title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
    </div>
    <div v-else>Нет фильмов с таким жанром на этой странице. Попробуйте еще раз.</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      genre: '',
      rating: '',
      year: '1990',
      type:'film',
      film: null,
      loading: false,
      whl:true,
      link_kp: '',
    };
  },
  methods: {
    async search() {
  this.loading = true;

  while (this.whl) {
    const page = Math.floor(Math.random() * 18000) + 1;
    const url = `https://kinobd.ru/api/films?page=${page}`;
    const response = await fetch(url);
    const data = await response.json();

    const filteredFilms = data.data.filter(film =>
      film.genres.some(genre => genre.name_ru.toLowerCase().includes(this.genre.toLowerCase()))
    ).filter(film => film.rating_kp_count > 1000); 
    console.log(filteredFilms);
    if (filteredFilms.length > 0) {
      let randomIndex = Math.floor(Math.random() * filteredFilms.length);
      let selectedFilm = filteredFilms[randomIndex];

      if (this.rating && selectedFilm.rating_kp < parseInt(this.rating)) {
        continue;
      }
      if (this.year && selectedFilm.year < parseInt(this.year)) {
        continue;
      }
      if (this.type !== selectedFilm.type ) {
        continue;
      }

      this.film = selectedFilm;
      break;
    }
  }

  this.loading = false;
  this.link_kp = 'https://www.kinopoisk.ru/film/'+this.film.kinopoisk_id
  console.log(this.link_kp)
},
  },
};
</script>





